#qa .leaf4 {
    left: 0%;
}

#qa .leaf5 {
    right: 0%;
    z-index: -1;
}

#qa .blocks {
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    /* gap: 3%; */
    /* color: white; */
}

#qa .blocks .block {
    /* border: solid 4px #2caad9; */
    text-align: left;
    /* padding: 5%; */
    line-height: 1.5em;
    border-radius: 20px;
}

#qa .blocks .block .pic {
    height: 150px;
}

#qa .blocks .block img {
    display: inline-block;
    position: relative;
    border-radius: 100%;
    /* box-shadow: 5px 5px 20px 10px rgba(0, 0, 0, 0.2); */
    top: -50%;
}

#qa .blocks .block h3 {
    color: #2caad9;
}

#qa .blocks .block h2 {
    font-size: x-large;
}

#qa .block1 {
    grid-column: 2;
    /* background-image: url("../img/yaaf1.png"); */
    background-repeat: no-repeat;
    background-position: center;

}

#qa .block2 {
    grid-column: 3;
    /* background-image: url("../img/yaaf2.png"); */
    background-repeat: no-repeat;
    background-position: center;
}

#qa .block3 {
    grid-column: 4;
    /* background-image: url("../img/yaaf3.png"); */
    background-repeat: no-repeat;
    background-position: center;
}

#qa .block .qa1-3-text {
    text-align: center;
    font-size: medium;
}


