.App {
  text-align: center;
  padding-left: calc(100vw - 100%);
}

* {
  font-family: Noto Sans TC;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  /* background: radial-gradient(46.34% 46.34% at 50% 27%, #6F139A 0%, #371B64 100%); */
  /* background: radial-gradient(119.2% 46.34% at 50% 27%, #6F139A 0%, #371B64 50%,#6F139A 100%); */
  /* background: radial-gradient(circle, rgba(111,19,154,1) 0%, rgba(55,27,100,1) 100%);  */
  /* background: radial-gradient(circle, #371b64 0%, #371b64 100%) */
}

button {
  height: 40px;
  border: none;
  border-radius: 20px;
  background-color: transparent;
  cursor: pointer;
  color: white;
  padding: 5px 20px;
}

select {
  height: 40px;
  border: solid 1px white;
  border-radius: 20px;
  background-color: transparent;
  cursor: pointer;
  color: white;
  padding: 5px 10px;
}

option {
  color: black;
}

.connect {
  background-color: #72c81d;
}

#nav {
  display: grid;
  grid-template-columns: 24% 1fr 12% 12%;
  justify-content: center;
  padding: 5%;
  padding-bottom: 0;
}

.leaf {
  position: absolute;
}

#nav .leaf1 {
  top: 0;
}

#nav .leaf2 {
  top: 0;
  right: 0%;
}

#nav .logo {
  display: inline-block;
  grid-column: 2;
  justify-content: center;
  display: grid;
}

#nav .switch {
  grid-column: 3;
  display: grid;
  justify-content: end;
  z-index: 2;
}

#nav .button {
  grid-column: 4;
  z-index: 2;
}

#nav select {
  vertical-align: top;
  margin-right: 1vw;
}

#nav button {
  /* margin-left: 20px; */
  vertical-align: top;
}

/* #countdown_block {
