}

#countdown_block .leaf3 {
  left: 0%;
} */
.countdown_block {
  padding-top: 50px;
}

.countdown_block ul {
  text-align: center;
  /* color: white; */
  font-size: 2rem;
  padding: 0;
}

.countdown_block li {
  width: 12vw;
  height: 12vw;
  min-width: 64px;
  max-width: 110px;
  min-height: 64px;
  max-height: 110px;
  border: solid 2px #2caad9;
  border-radius: 20px;
  list-style-type: none;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  display: inline-block;
  background-color: rgba(127, 127, 127, 0.1);
  box-sizing: revert;
}

.countdown_block li span {
  display: block;
  font-size: 2rem;
}

.countdown_block li .time {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 100%;

}

.countdown_block li .text {
  width: 10vw;
  min-width: 17px;
  max-width: 30px;
  display: inline-block;
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  font-size: 16px;
  top: -12px;
  padding: 0px 10px;
  border-radius: 10px;
  background-color: #2caad9;
  /* color: black; */
  font-weight: bold;
  box-sizing: revert;
}


#yaaf {
  display: grid;
  grid-template-columns: 10% 39% 2% 39% 10%;
  text-align: left;
  margin-top: 30px;
}

#yaaf .block {
  border-radius: 20px;
  background-color: rgba(127, 127, 127, 0.06);
  padding: 40px 50px;
  border: #2caad9 3px solid;
  /* color: white; */
}

#yaaf .block1 {
  grid-column: 2;
}

#yaaf .block2 {
  grid-column: 4;
}

#yaaf .col {
  font-size: medium;
  display: grid;
  padding-top: 8px;
  padding-bottom: 6px;
  font-weight: bold;

}

