}

#yaaf .right {
  grid-column: 2;
  text-align: right;
  font-family: Rubik;
}

#yaaf .block2 input {
  text-align: center;
  height: 40px;
  width: 80%;
  /* font-size: 20px; */
}

#yaaf button {
  background-color: grey;
  border-radius: 10px;
  height: 60px;
  font-weight: bold;
  font-size: medium;
  margin-top: 10px;
  margin-bottom: 10px;
}

#yaaf .block1 .buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3%
}

#yaaf input {
  width: 100%;
