.heading {
  display: grid;
  justify-content: center;
  padding: 5%;
}

.heading .block {
  vertical-align: top;
}

.heading .block h1 {
  font-size: xx-large;
  display: inline-block;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 10px;
  margin-right: 10px;
  /* color: white; */
}

.heading .icon {
  display: inline-block;
  justify-content: center;
  align-items: center;
}

#team .leaf4 {
  left: 0%;
}

#team .leaf5 {
  right: 0%;
  z-index: -1;
}

#team .blocks {
  display: grid;
  grid-template-columns: 1fr 3fr 3fr 3fr 1fr;
  gap: 3%;
  /* color: white; */
}

#team .blocks .block {
  border: solid 8px #2caad9;
  text-align: center;
  padding: 5%;
  line-height: 1.5em;
  border-radius: 20px;
}

#team .blocks .block .pic {
  height: 150px;
}

#team .blocks .block img {
  display: inline-block;
  position: relative;
  border-radius: 100%;
  /* box-shadow: 5px 5px 20px 10px rgba(0, 0, 0, 0.2); */
  top: -50%;
}

#team .blocks .block h3 {
  color: #2caad9;
}

#team .blocks .block h2 {
  font-size: x-large;
}

#team .block1 {
  grid-column: 2;
  /* background-image: url("../img/yaaf1.png"); */
  background-repeat: no-repeat;
  background-position: center;

}

#team .block2 {
  grid-column: 3;
  /* background-image: url("../img/yaaf2.png"); */
  background-repeat: no-repeat;
  background-position: center;
}

#team .block3 {
  grid-column: 4;
  /* background-image: url("../img/yaaf3.png"); */
  background-repeat: no-repeat;
  background-position: center;
}

#roadmap .leaf6 {
  right: 0%;
  z-index: -1;
}

#roadmap .blocks {
  display: grid;
  grid-template-columns: 1fr 3fr 3fr 3fr 3fr 1fr;
  gap: 2%;
  /* color: white; */
}

#roadmap .blocks .block {
  text-align: center;
  padding: 25px;
  line-height: 1.5em;
  font-size: medium;
  border-radius: 20px;
  background-color: rgba(127, 127, 127, 0.1);
  padding-top: 0;
  border: 4px solid #2caad9;
}

#roadmap .blocks .block h2 {
  /* font-family: Rubik; */
  font-size: large;
  position: relative;
  margin: 0;
  top: -22px;
  border-radius: 20px;
  display: inline-block;
  padding: 10px 25px;
}

#roadmap .blocks .block .pic {
  padding: 40px;
}

#roadmap .blocks .block .pic img {
  width: 120px;
}

#roadmap .block1 {
  grid-column: 2;
}

#roadmap .block2 {
  grid-column: 3;
}

#roadmap .block3 {
  grid-column: 4;
}

#roadmap .block4 {
  grid-column: 5;
}

#roadmap .block1 h2 {
  background-color: #7986cb;
  /* color: black; */
}

#roadmap .block2 h2 {
  background-color: #72c81d;
  /* color: black; */
}

#roadmap .block3 h2 {
  background-color: #fd40c8;
  /* color: black; */
}

#roadmap .block4 h2 {
  background-color: #ff9600;
  /* color: black; */
}

#footer .leaf7 {
  left: 10vw;
  z-index: -1;
  /* bottom: -10%; */
}

#footer {
  padding-top: 7%;
  /* padding-bottom: 7%; */
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
}

#footer .icons {
  grid-column: 2;
  text-align: center;
}

#footer a {
  padding-left: 10px;
  padding-right: 10px;
}
#footer img{
  width: 40px;
  height: 40px;
}

@media screen and (max-width:1439px) {

  #roadmap .blocks {
    grid-template: 50% 50% / 2fr 3fr 3fr 2fr;
  }

  #roadmap .block1 {
    grid-area: 1/2;
  }

  #roadmap .block2 {
    grid-area: 1/3;
  }

  #roadmap .block3 {
    grid-area: 2/2;
  }

  #roadmap .block4 {
    grid-area: 2/3;
  }
}

@media screen and (max-width:1024px) {
  #nav {
    grid-template: 20% 80% / repeat(10, 1fr);
  }

  #nav .switch {
    grid-area: 1 / 2 / 2 / 4;
    justify-content: center;
  }

  #nav .button {
    grid-area: 1 / 8 / 2 / 10;
  }

  #nav select {
    width: 100%;
  }

  #nav button {
    width: 100%;
  }

  #nav .logo {
    grid-area: 2 / 4 / 3 / 8;
  }

  li .text {
    font-size: 14px;
    padding: 6x 10px;
    top: -7px;
  }

  #yaaf {
    grid-template: 1fr 1fr/5% 1fr 5%;
    gap: 5%;
  }

  #yaaf .block1 {
    grid-area: 1/2;
  }

  #yaaf .block2 {
    grid-area: 2/2;
  }

  #yaafref {
    grid-template: 1fr 1fr/5% 1fr 5%;
    gap: 5%;
  }

  #yaafref .block1 {
    grid-area: 1/2;
  }

  #yaafref .block2 {
    grid-area: 2/2;
  }

  #team .blocks {
    grid-template: 1fr 1fr 1fr/5% 1fr 5%;
  }

  #team .block1 {
    grid-area: 1/2;
  }

  #team .block2 {
    grid-area: 2/2;
  }

  #team .block3 {
    grid-area: 3/2;
  }

  #roadmap .blocks {
    grid-template: 1fr 1fr 1fr 1fr/5% 1fr 5%;
  }

  .heading {
    margin-top: 10%;
    margin-bottom: 10%;
  }

  #roadmap .block1 {
    grid-area: 1/2;
  }

  #roadmap .block2 {
    grid-area: 2/2;
  }

  #roadmap .block3 {
    grid-area: 3/2;
  }

  #roadmap .block4 {
    grid-area: 4/2;
  }

  #footer {
    margin-top: 15%;
  }
}

@media screen and (max-width:768px) {
  .leaf {
    display: none;
  }
}

@media screen and (max-width:600px) {
  #nav .switch {
    grid-area: 1 / 2 / 2 / 5;
  }

  #nav .button {
    grid-area: 1 / 7 / 2 / 10;
  }

}

@media screen and (max-width: 426px) {
  #nav {
    grid-template: 20% 80% / repeat(10, 1fr);
  }

  #nav .switch {
    grid-area: 1 / 1 / 2 / 5;
  }

  #nav .button {
    grid-area: 1 / 7 / 2 / 11;
  }

  #nav select {
    width: 100%;
  }

  #nav button {
    width: 100%;
  }

  #nav .logo {
    grid-area: 2 / 4 / 3 / 8;
  }

  li .text {
    font-size: 12px;
    padding: 3px 3px;
    top: -6px;
  }

  #yaaf {
    grid-template: 1fr 1fr/5% 1fr 2.5%;
  }

  #yaaf .block {
    padding: 40px 30px;
  }

  #yaaf img {
    width: 100px;
    padding-top: 30px;
  }

  #yaaf .infoblock .col {
    font-size: 16px;
  }

  #yaafref {
    grid-template: 1fr 1fr/5% 1fr 2.5%;
    padding-top: 60px;
  }

  #yaafref .block {
    padding: 40px 30px;
  }

  #yaafref img {
    height: 130px;
    width: 130px;
  }

  #yaafref .infoblock .col {
    font-size: 16px;
  }

  .heading .block h1 {
    font-size: xx-large;
  }

  #team .blocks {
    gap: 6%;
  }

  #roadmap .heading {
    margin-top: 30%;
  }

  #roadmap .blocks {
    gap: 5%;
  }

  #footer {
    margin-top: 60%;
  }

}